



.about{
    min-height: 800px;
}

.about__wrapper{
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: row;
    // margin-bottom: 64px;


    @include bpmax($m) {
        flex-direction: column;
        align-items: stretch;
    }
}

.about__rightWrapper{
    background-color: $color-white;
    background-size: 42px 42px;
    background-image:  repeating-linear-gradient(0deg, $color-light-3, $color-light-3 2.1px, $color-white 2.1px, $color-white);

    padding: 32px;
    width: 65%;

    @include bpmax($m) {
        width: 100%;
    }
}

.about__title{
    @extend %h1;
    margin-top: 96px;
    margin-bottom: 32px;
    @extend %tracking-in-expand;

    @include bpmax($m) {
        margin-top: 0px;
    }
}

.about__sectionTitle{
    @extend %h2;
    text-align: center;
    margin-bottom: 32px;
}
.about__section{
    padding-top: 80px;
    // margin-bottom: 32px;

    @include bpmax($m) {
        padding-top: 32px;
    }
}

.about__images{
    display: flex;
    justify-content: space-between;
    margin: 0 64px;

    @include bpmax($m) {
        margin: 0;
    }
}

.about__imgItem{
    max-width: 30%;
}

.about__paragraph{
    @extend %paragraph--serif;
    margin-bottom: 64px;
    padding: 0 64px;
    text-align: justify;
    text-justify: inter-word;

    @include bpmax($m) {
        padding: 0px;
    }
}

.about__leftWrapper{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    width: 35%;
    position: sticky;
    top: 0;
    margin-bottom: 96px;
    left: 0;

    @include bpmax($m) {
        position: relative;
        width: 100%;
        margin-bottom: 0;
    }
}

.about__img{
    margin-top: 80px;
    padding-top: 100px;
    max-width: 50%;

    @include bpmax($m) {
        padding-top: 32px;
    }
}

.about__item{
    margin-bottom: 16px;
    position: relative;
	display: inline-block;
	white-space: nowrap;
	text-align: center;
    z-index: 10;

    &:hover{
        opacity: 0.5;
    }

	&:after,
	&:before {
		content: " ";
		display: block;
		position: absolute;
		bottom: -.5em;
		width: 1em;
		height: 100%;
		background-color: lighten($color-brown-grey,10);
		pointer-events:none;
	}

	&:after {
		right: -.57em;
		z-index: -1;
	}

	&:before {
		bottom: auto;
		top: -.5em;
		left: -.57em;
		z-index: -1;
	}

}

.about__button{
    width: 200px;
    font-size: rem(20px);
    line-height: rem(28px);
    display: inline-block;
    position: relative;
    padding: 0.45em .75em;
    text-decoration: none;
    color: darken($color-white,0);
    background-color: lighten($color-brown-grey,20);
    cursor: pointer;

    &:after,
    &:before {
        content: " ";
        display: block;
        position: absolute;
        width: 0;
        height: 0;
        bottom: -.5em;
        left: -.05em;
        pointer-events: none;
        -webkit-mask: none;
        mask: none;
    }

    &:before {
        bottom: auto;
        top: -.5em;
        border-bottom: .5em solid darken($color-brown-grey,10);
        border-left: .5em solid transparent;
    }

    &:after {
        left:  auto;
        right: -.05em;
        border-top: .5em solid darken($color-brown-grey,10);
        border-right: .5em solid transparent;
    }
}

.about__list{
    margin-top: 64px;
    display: flex;
    flex-direction: column;
}